




















/* eslint-disable @typescript-eslint/ban-ts-comment */
import Vue from 'vue'
import { VHotkey } from '@/directives'
import { KBarAction } from '@/interfaces'

import VKbarContent from './VKbarContent.vue'
import VKbarSearch from './VKbarSearch.vue'
import VKbarResult from './VKbarResult.vue'

type Data = {
  show: boolean
  search: string
}

type Props = {
  actions: KBarAction[]
}

type Computed = {}

type Methods = {
  onToggle: () => void
  closeKbar: () => void
  clickOnResult: () => void
}

export default Vue.extend<Data, Methods, Computed, Props>({
  name: 'VKbar',
  components: {
    VKbarContent,
    VKbarSearch,
    VKbarResult
  },
  props: {
    actions: {}
  },
  data: () => ({
    show: false,
    search: '',
    hotkey: {
      hotkey: 'Control+k,Meta+k',
      handler: 'onToggle'
    }
  }),
  directives: {
    hotkey: VHotkey
  },
  provide () {
    return {
      kbar: this
    }
  },
  watch: {
    show: {
      immediate: true,
      handler (state) {
        if (!state) {
          this.search = ''
        }
      }
    }
  },
  methods: {
    closeKbar () {
      this.show = false
    },
    onToggle () {
      console.log('onToggle')
      this.show = !this.show
    },
    clickOnResult () {
      this.closeKbar()
    }
  }
})
