

















import Vue from 'vue'
import { VHotkey } from '@/directives'
import { KBarAction } from '@/interfaces'

type Props = {
  item: KBarAction
}

export default Vue.extend<{}, {}, {}, Props>({
  name: 'VKbarAction',
  props: {
    item: {}
  },
  directives: {
    hotkey: VHotkey
  },
  computed: {
    hotkey () {
      const shortcut = this.item.shortcut?.join('+')
      return {
        hotkey: `Control+${shortcut}`
      }
    }
  },
  methods: {
    handleClick () {
      this.$emit('click', this.item)
    }
  }
})
